import React,{ FC, useEffect,useContext } from 'react'
import { useNavigate } from 'react-router-dom'
import { ContextProps } from '../../App'
import { Container } from '.'
import { actionCreator as dashBoardActionCreator } from '../DashBoard'
import { actionCreator as LoginActionCreator } from '../Login'
import pigeonClubCodeList from '../../asset/pigeonClubCodeList.json' // 臨時設定後端 api 完成後會移除

interface itemType {
    pigeonClubID: string,
    pigeonClubArea: string
    pigeonClubName: string
    pigeonClubCode: string
}

const ClubListForAdminLogin:FC = ():JSX.Element => {
    const router = useNavigate()
    const { $,setReducer,getReducer } = useContext(ContextProps)

    const { loginInfo } = getReducer(({ login }) => ({ loginInfo:login.loginInfo }))

    const goPage:(clubCode:string) => void = clubCode => {
        setReducer(LoginActionCreator,'setLoginInfo',{ ...loginInfo,fromListClub: clubCode })
        setReducer(dashBoardActionCreator, 'setChoiceDate', loginInfo.currentDate)
        router({ pathname: '/dashboard/current_back' })
    }

    useEffect(() => {
        if(!sessionStorage.getItem('f_token')) router({ pathname:'/login' })
    }, [])

    return (
        <Container>
            <div className="container">
                <div className="list-outer">
                    <div className="row g-2">
                        <div className="col-md-4">
                            <div className="list-item" onClick={() => router({ pathname:'/d_end/register' })}>會員管理</div>
                        </div>
                        {$.maps(pigeonClubCodeList,({ pigeonClubCode,pigeonClubName,pigeonClubArea }:itemType,index:number) => (
                            <div className='col-md-4' key={index}>
                                <div onClick={goPage.bind(this,pigeonClubCode)} className='list-item'>{pigeonClubCode}-{pigeonClubArea}-{pigeonClubName}</div>
                            </div>
                        ))}
                    </div>
                </div>
            </div>
        </Container>
    )

}

export default ClubListForAdminLogin